<template>
	<div class="wrapper">
		<v-form 
			ref="form"
			@submit.prevent="submitForm"
		>
			<v-container>
				<v-row>
					<v-col cols="12">
						<v-text-field
							v-model="template.name"
							label="Название"
							:rules="generalRules"
							required
						/>
						<div id="template_content">
							<v-textarea
								v-model="template.text"
								rows="1"
								:disabled="isUploadFile"
								:no-resize="isUploadFile"
								:rules="isUploadFile ? [] : generalRules"
								label="Текст"
								required
							/>
							<v-switch
								v-model="isUploadFile"
								:label="'Загрузить файл'"
							/>
							<v-file-input
								v-model="template.attachment"
								show-size
								:disabled="!isUploadFile"
								:rules="isUploadFile ? generalRules : []"
								label="Файл"
								accept="*"
							/>
						</div>
						<v-textarea
							id="template-cb"
							v-model="template.clientsNumbers"
							rows="1"
							label="Клиентская база"
							:rules="generalRules"
							hint="Указывайте номера телефонов через запятую, без пробелов, знаков '+', 8 в начале"
							placeholder="77471234567,77071234567"
							required
							@keydown.enter.prevent
							@keydown.space.prevent
						/>
					</v-col>
				</v-row>
				<v-row>
					<v-col cols="12">
						<v-btn
							id="template_submit"	
							type="submit"
							color="primary"
						>
							Отправить
						</v-btn>
					</v-col>
				</v-row>
			</v-container>
		</v-form>
		<vue-element-loading 
			:active="isLoading"
			:background-color="'rgba(255, 255, 255, .2)'"
			:color="'blue'"
		/>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import { HTTP } from '@/helpers/httpCommon';
export default {
	data() {
		return {
			template: {
				name: '',
				text: '',
				attachment: null,
				clientsNumbers: '',
			},
			isUploadFile: false,
			isLoading: false,
			generalRules: [
				v => !!v || '*Обязательное поле',
			],
		};
	},
	computed: {
		...mapGetters(['getUserId']),
		myUserId() {
			return this.getUserId;
		}
	},
	methods: {
		validate () {
			return this.$refs.form.validate();
		},
		reset () {
			this.$refs.form.reset();
		},
		submitForm() {
			if(this.validate()) {
				this.isLoading = true;

				const id = this.$route.params.userId;
				const content = {};
				const formData = new FormData();

				if(id) formData.append('childId', id); 

				if(this.isUploadFile) {
					delete this.template.text;
					formData.append('attachment', this.template.attachment);
					content.type = 'FILE';
				} else {
					delete this.template.attachment;
					formData.append('text', this.template.text);
					content.type = 'TEXT';
					content.data = this.template.text;
				}

				formData.append('name', this.template.name);
				formData.append('cb', this.template.clientsNumbers);
				
				HTTP.post('/template/send', formData)
					.then((response) => {
						this.$store.commit('setTemplate', response.data.Data);
						if(id) this.$store.commit('setActiveNestedDashboardCategory', 'list');
						else this.$store.commit('setActiveDashboardCategory', 'list');
						this.$toast.success('Шаблон успешно создан!', {
							timeout: 2000
						});
						window.templateCreate = true;
						this.reset();
					})
					.catch(error => {
						this.$toast.error(error.message, {
							timeout: 2000
						});
					})
					.finally(() => { 
						this.isLoading = false;
					});

			} else {
				this.$toast.error('Обязательные поля не заполнены!', {
					timeout: 2000
				});
			}
		},
	}
};
</script>